$primary-color: #e5eefd;
$secondary-color: #424242;

.container {
  margin: 0 auto;
  max-width: 500px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}

h2 {
  margin-bottom: 20px;
  color:$secondary-color;
}

form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 20px;

  label {
    display: block;
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 500;
    color: $secondary-color;
  }

  input,
  textarea {
    padding: 10px;
    border: none;
    background-color: #f4f4f4;
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);

    &:hover {
      background-color: #e9e9e9;
    }

    &:focus {
      outline: none;
      background-color: #fff;
      box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    }
  }

  input[type="submit"] {
    background-color: $primary-color;
    color: #fff;
    border-radius: 5px;
    padding: 10px;
    font-size: 16px;
    font-weight: 500;
    border: none;
    cursor: pointer;

    &:hover {
      background-color: darken($primary-color, 10%);
    }
  }
}

.form-submit {
  background-color: #25D366; // Verde WhatsApp
  color: #FFFFFF; // Texto blanco
  border: none; // Sin borde
  border-radius: 5px; // Bordes redondeados
  padding: 10px 20px; // Espacio dentro del botón
  font-size: 16px; // Tamaño de fuente
  text-align: center; // Alineación de texto
  text-decoration: none; // Sin subrayado de enlace
  display: inline-block; // Bloque de visualización
  transition-duration: 0.3s; // Duración de la transición al pasar el ratón
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1); // Sombra suave
  &:hover {
    background-color: #22B358; // Cambio de color al pasar el ratón
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.3); // Sombra más intensa
  }
}